import React from 'react';
import { IconEdit } from '@ali/4ever-component';
import { BiPlugin } from '@ali/4ever-cangjie';
import { TemplateButtonRole } from '../../utils/role';
import { TemplateButtonConfig } from '../../type';
import { TemplateButton } from '../../mo';

export const createRenderHoverToolbar = (
  config: TemplateButtonConfig
): BiPlugin['renderHoverToolbar'] => {
  const { locale } = config || {};
  return (props, controller, getNode) => {
    const { buttonName } = props;
    switch (buttonName) {
      case 'edit':
        return {
          title: locale?.editTooltip || 'Configuration Template',
          tooltip: locale?.editTooltip || 'Configuration Template',
          icon: <IconEdit />,
          onClick: () => {
            const node = getNode();
            if (!TemplateButton.isTemplateButton(node)) return;
            controller.dispatch('toggleTemplateButton', { key: node.key });
          },
          testid: 'templateButton-edit',
          role: TemplateButtonRole.templateButton,
        };
      default: return null;
    }
  };
}
